@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
#logo {
    color: #fff;
    padding: 10px 20px;
    font-size: 20px;
    margin-left: 15px;
    text-decoration: none;
}

nav {
    position: fixed;
    background-color: #1c2237;
    height: 60px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 3;
}

.nav_menu{
    margin: 0 20px;
    margin-top: -2px;
    display: flex;
    list-style-type: none;
}

.nav_menu a{
    display: block;
    text-decoration: none;
    color: white;
    padding: 10px 20px;
    margin-left: 5px;
    padding-bottom: 10px;
}

.nav_item::after{
    content: "";
    position: relative;
    left: 50%;
    display: block;
    height: 2px;
    width: 0;
    background: transparent;
    transition: 0.4s;
}

.nav_item:hover::after{
    left: 0%;
    width: 100%;
    background-color: white;
}

.nav_icon {
    display: none;
    cursor: pointer;
    padding: 10px 20px;
    color: white;
}

@media screen and (max-width: 700px){
    .nav_menu{
        flex-direction: column;
        position: absolute;
        width: 100vw;
        right: -110%;
        top: 60px;
        opacity: 1;
    }
    .nav_menu.active{
        background: #1c2237;
        right: -20px;
        transition: right 0.4s ease;
        z-index: 4;
        text-align: center;
        border-bottom: 3px solid white;
    }
    .nav_item{
        padding: 20px 10px;
    }
    .nav_item:hover::after{
        background: transparent;
        height: 0;
    }
    .nav_icon{
        display: block;
    }
}
#home {
    color: white;
    background-color: #1c2237;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.home_content p:first-child {
    font-size: 30px;
}

.home_content p {
    padding: 5px 0px;
}

.btn {
    display: block;
    position: absolute;
    bottom: 60px;
    z-index: 0;
    animation: updown 4s ease-in-out infinite;
    width: 40px;
}

#home #sumir{
    margin-top: 100px;
    display: flex;
    flex-direction: column;
}

.icons {
    margin: -5px;
    position: relative;
    height: 100px;
    width: 100px;
}

.icons:nth-of-type(1){
    top: 20px;
    right: -50px;
    width: 200px;
    height: 200px;
}
.icons:nth-of-type(2){
    right: 160px;
    top: -75px;
    width: 150px;
    height: 150px;
}
.icons:nth-of-type(3){
    top: -20px;
    right: 85px;
    width: 140px;
    height: 140px;
}
.icons:nth-of-type(4){
    top: -180px;
    right: -120px;
    width: 125px;
    height: 125px;
}


@keyframes updown {
    0% {
        transform: translateY(50%);
    }

    50% {
        transform: translateY(-75%);
    }

    100% {
        transform: translateY(50%);
    }

}

#home a {
    text-decoration: none;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 700px) {
    #contato #home{
        justify-content: center;
    }
    #sumir{
        display: none;
    }
    .btn {
        animation: none;
    }
}

@media screen and (max-width: 800px){
    #home #sumir{
        display: none;
    }
    .icons{
        display: none;
    }
}

@media screen and (max-height: 500px) {
    .btn {
        visibility: hidden;
    }
}
#sobre {
    display: flex;
    align-items: baseline;
    justify-content: space-around;
    height: 100vh;
    background-color: #fff;
}

.sobre_conteudo {
    width: 50%;
    margin-left: 40px;
    padding: 50px;
    margin-top: 150px;
}

.sobre_conteudo h1 {
    padding-bottom: 15px;
    letter-spacing: 1px;
}

.sobre_conteudo p {
    text-shadow: 1px black  ;
    font-size: 1.13em;
}

.sobre_conteudo p:first-of-type {
    padding-bottom: 20px;
}

#sobre a {
    text-decoration: none;
    padding: 10px 15px;
    margin: 5px 10px 5px 0;
    display: inline-block;
    background-color: rgb(219, 203, 203);
    border-radius: 5px;
    color: black;
    font-weight: 550;
    cursor: pointer;
}

@media screen and (max-width: 700px) {
    #sobre {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100vh;
        padding: 0 20px;
    }

    .sobre_conteudo {
        width: 100%;
        margin: 0 10px;
        padding: 0;
    }

    .sobre_conteudo:first-child {

        padding-bottom: 100px;
    }
}

@media screen and (max-height: 500px) {
    #sobre {
        height: 120vh;
        align-items: center;
    }

    #sobre div {
        width: 100%;
        margin: 0 10px;
        padding: 0;
    }
}
#projetos{
    background-color: #f1f1f1;
}

.big_p_container {
    height: 140vh;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 20vh repeat(12, 1fr);
    color: #000;
}

.p_titulo {
    font-size: 25px;
    padding-top: 40px;
    height: 20vh;
    grid-column: span 12;
    display: flex;
    align-items: center;
    justify-content: center;
}

.p_container {
    grid-column: span 12;
    grid-row: span 0;
    display: flex;
    justify-content: space-evenly;
}

.p_semi_container {
    border-radius: 5px;
    background-color: #fff;
    margin-bottom: 30px;
    width: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    transition: 0.3s;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

#projetos img {
    margin: 20px 0px;
    width: 100%;
    height: 100%;
    background-color: #fff;
}

.p_legendas {
    color: #000;
    padding: 10px 15px 0 15px;
    text-align: center;
}

.p_legendas h1 {
    padding-top: 40px;
    padding-bottom: 5px;
    font-size: 40px;
}

.p_legendas p {
    padding-bottom: 10px;
    font-size: 17px;
}

@media screen and (max-width: 800px) {
    #projetos {
        height: 100%;
    }

    .p_titulo{
        padding-top: 50px;
        height: 10vh;
    }

    .p_container {
        flex-direction: column;
        align-items: center;
        height: 100%;
        justify-content: flex-start;
    }

    .p_semi_container {
        box-shadow: rgba(157, 168, 180, 0.2) 0px 8px 24px;
        height: 40%;
        width: 70%;
        margin: 0px 20px 40px 20px;
        border-radius: 0;
    }

    #projetos img {
        height: 100%;
        border: none;
    }

    .p_legendas h1 {
        padding-top: 0
    }

    .p_legendas {
        padding-top: 0;
    }

}

@media screen and (max-width: 500px) {
    .p_semi_container {
        width: 90%;
        justify-content: center;
        background-color: rgb(255, 255, 255);
    }
    .p_legendas h1{
        padding-top: 60px;
    }
    #projetos img{
        margin-bottom: 0;
    }
}
#contato{
    display: flex;
    height: calc(100vh);
    color: #fff;
    background-color: #1c2237;
    justify-content: space-around;
}

#contato div{
    text-align: center;
}

#contato_conteudo{
    display: flex;
    justify-content: center;
    flex-direction: column;
}

#contato_conteudo div:first-child{
    padding-bottom: 100px;
}

#contato_conteudo div h1{
    padding-bottom: 10px;
}

.contato_img{
    top: 40px;
    width: 80%;
    align-self: center;
    padding-top: 150px;
}

.container_icon{
    margin-top: 50px;
    display: flex;
    justify-content: center;
}

.contato_icon{
    border-radius: 7px;
    margin: 0px 15px;
    width: 50px;
    height: 50px;
    font-size: 40px;
    color: #fff;
    background-color: #1c2237;
    transition: 0.5s;
}

.contato_icon:hover{
    color: #1c2237;
    background-color: #fff;
}

@media screen and (max-width: 700px){
    #contato div{
        width: 100%;
    }
}

@media screen and (min-width: 701px) and (max-width: 1280px){
    #contato div{
        width: 100%;
    }
}

@media screen and (max-height: 500px){
    #sumir{
        display: none;
    } 
}
#footer{
    height: 80px;
    background-color: #000;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
}
* {
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
    font-family: 'Montserrat', sans-serif;
}

body{
    background-color: #1c2237;
    overflow: auto;
}

::-webkit-scrollbar {
    width: 13px;
    background-color: white;
}

::-webkit-scrollbar-thumb {
    background-color: rgb(180, 180, 180);
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background-color: rgb(140, 140, 140);
}
